// スマホのみのフッターナビゲーション
.sm-nav {
  position: fixed;
  background: rgba($bg-color,.8);
  z-index: 100;
  width: 100%;
  bottom:0;
  margin-bottom: env(safe-area-inset-bottom);

  & ul{

    display: flex;
    justify-content: space-between;


    & a{
      display: flex;
      flex-direction: column;
      margin: 10px;
      align-content: center;
      text-align: center;
      flex: 1;

        & span{
          font-size: 14px;
        }
    }
  }
}

// もっと読むボタン


@media only screen and (min-width: $breakpoint-md) {

  .sm-nav{
  display:none;
  }
}
/* MediaQuery end */
